import React, { useEffect } from "react";
import CatItems from "./CatItems";
import { useDispatch, useSelector } from "react-redux";
import { getCategories } from "../Redux/Action/CategoryAction";
import Axios from "axios";

const Catogories = () => {
  const dispatch = useDispatch();

  useEffect(
    () => async () => {
      await Axios({
        mathod: "GET",
        url: "http://127.0.0.1:8000/api/shop/category/",
      }).then((response) => {
        console.log(response.data);
        dispatch(getCategories(response.data));
      });
    },
    []
  );

  const catogories = useSelector((state) => state.catogorie.catogoryName);

  return (
    <div
      className="container-fluid categoryDiv"
      style={{ background: "antiquewhite" }}
    >
      <div className="row">
        <div className=" col-md-12 col-lg-12  m-1 ">
          <div className="container">
            <div className="row">
              {catogories.map((cat) => {
                return <CatItems cat={cat}></CatItems>;
              })}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Catogories;
