import React, { Fragment, useEffect, useState } from "react";
import { Nav, Navbar } from "react-bootstrap";

import { HiShoppingCart } from "react-icons/hi";

import { useSelector, useDispatch } from "react-redux";

import { NavLink } from "react-router-dom";

import { Link, Redirect } from "react-router-dom";

import { logout } from "../Redux/Action/auth";

// import format from "../format";

import { FiUser } from "react-icons/fi";

export default function Header() {
  const dispatch = useDispatch();
  const [redirect, setRedirect] = useState(false);
  const [cart, setCart] = useState(false);
  const showCartItems = () => {
    setCart(!cart);
  };

  const isAuthenticated = useSelector((state) => state.auth.isAuthenticated);

  // const cartItems = useSelector((state) => state.cart.cartItems);

  // const totalAmount = useSelector((state) => state.cart.totalAmount);

  const logout_user = () => {
    dispatch(logout());
    setRedirect(true);
  };

  const guestLinks = () => (
    <Fragment>
      <li className="nav-item">
        <Link className="nav-link" to="/login">
          Login
      </Link>
      </li>
      <li className="nav-item">
        <Link className="nav-link" to="/signup">
          Sign Up
      </Link>
      </li>
    </Fragment>
  );

  const authLinks = () => (
    <Fragment>
      <li className="nav-item">
        <a className="nav-link" href="#!" onClick={logout_user}>
          Logout
      </a>
      </li>
    </Fragment>
  );

  return (
    <div>
      <Navbar bg="dark" variant="dark">
        {isAuthenticated ? (
          <>
            <Nav className="mr-auto nav ">


              <NavLink
                className="  navLink navLink1"
                activeClassName="navbar__link--active"
                to="/home"
              >
                Home
              </NavLink>

              <NavLink
                activeClassName="navbar__link--active"
                className=" navLink navLink2"
                to="/products"
              >
                Products
              </NavLink>

              <NavLink
                activeClassName="navbar__link--active"
                className=" navLink navLink3"
                to="/category"
              >
                Categories
              </NavLink>

              <NavLink
                activeClassName="navbar__link--active"
                className=" navLink navLink4"
                to="/what-new"
              >
                What's New
              </NavLink>
              <div class="animation start-home"></div>
            </Nav>

            <div className="d-flex justify content between">
              <div>
                <button className="btn cartBtn" onClick={showCartItems}>
                  CART ITEMS
                      <span class="reactCartIcon">
                    <HiShoppingCart />
                    {/* <span className="cartNum">({cartItems.length})</span> */}
                  </span>
                </button>
              </div>
            </div>

            <button className="btn btn-primary" href="#!" onClick={logout_user}>
                  Logout
            </button >
          </>

        ) : (
          <>
            <Nav className="mr-auto nav ">
              <NavLink
                activeClassName="navbar__link--active"
                className=" navLink navLink2" to="/login">
                Login
              </NavLink>


              <NavLink
                activeClassName="navbar__link--active"
                className=" navLink navLink2"to="/signup">
                Sign Up
              </NavLink>
              <div class="animation start-home"></div>
            </Nav> 
          </> 
        )}

            {cart && (
              <div id="cart" className="cart  ">
                <ul>
                  {/* {cartItems.map((items) => {
                        return <Cartitems items={items}></Cartitems>;
                      })} */}
                </ul>

                <div className="cart-total-container d-flex justify-content-around text-capitalize mt-5">
                  <h5>TOTAL :</h5>
                  <h5>
                    <strong id="cart-total" className="font-weight-bold">
                      {/* {format(totalAmount)} */}
                    </strong>
                  </h5>
                </div>

                <div className="cart-buttons-container mt-3 d-flex justify-content-between">
                  {/* <a
                        // onClick={() => dispatch(clearCart())}
                        id="clear-cart"
                        className="btn btn-outline-secondary btn-black text-uppercase"
                      >
                        clear cart
                      </a> */}
                  <Link
                    to={"/check"}
                    className="btn btn-outline-secondary text-uppercase btn-pink"
                  >
                    checkout
                      </Link>
                </div>
              </div>
            )}


          </Navbar>
      {redirect ? <Redirect to="/" /> : <Fragment></Fragment>}
    </div>
  );
}
